import React from "react";
import "./RecommendedVideos.css";
import VideoCard from "./VideoCard";
import Firebase from 'firebase';

export default class RecommendedVideos extends React.Component {
  
  constructor(props){
    super(props);
    this.state = {videos: []};
  }

    async componentDidMount(){
      var finalVideos;
      await Firebase.database().ref('/').once('value').then(function(snapshot){ 
        var videoData = snapshot.val().items;
        console.log(videoData);
        var videos = [];
        for(let video of videoData){
        let currVideo = <VideoCard 
        id = {video.id.videoId}
        title = {video.snippet.title}   
        image = {video.snippet.thumbnails.default.url}
        channel = {video.snippet.channelTitle}
        timestamp = {Math.ceil(Math.random()*10) + "days ago"}
        channelUrl = {"https://www.youtube.com/channel/"+video.snippet.channelId}
        >
        </VideoCard>
        videos.push(currVideo);
        
      }
      finalVideos = videos;
      });
      this.setState({videos: finalVideos});
    }

  render(){
  return (
    <div className="recommendedvideos">
      <div className="recommendedVideos__videos">
       {this.state.videos}   
        
      </div>
    </div>
  );
  }
}

